document.addEventListener("DOMContentLoaded", function() {
    // Adiciona um listener de evento ao botÃ£o de traduÃ§Ã£o que chama a funÃ§Ã£o translateText quando clicado.
    document.getElementById('translateButton').addEventListener('click', translateText);
});

function translateText() {
    // ObtÃ©m o texto inserido pelo usuÃ¡rio na Ã¡rea de texto com id 'inputText'.
    const inputText = document.getElementById('inputText').value;
    // ObtÃ©m o valor do idioma de entrada selecionado pelo usuÃ¡rio no seletor com id 'sourceLang'.
    const sourceLang = document.getElementById('sourceLang').value;
    // ObtÃ©m o valor do idioma de saÃ­da selecionado pelo usuÃ¡rio no seletor com id 'targetLang'.
    const targetLang = document.getElementById('targetLang').value;

    // Verifica se todos os campos foram preenchidos.
    if (!inputText || !sourceLang || !targetLang) {
        alert("Por favor, preencha todos os campos.");
        return;
    }

    // Adiciona um Ã­cone de carregamento enquanto a traduÃ§Ã£o estÃ¡ sendo realizada (pode ser uma animaÃ§Ã£o ou texto).
    document.getElementById('outputText').value = "Traduzindo...";

    // Envia uma requisiÃ§Ã£o HTTP POST para o endpoint '/translate'.
    fetch('/translate', {
        method: 'POST', // Define o mÃ©todo HTTP como POST.
        headers: {
            'Content-Type': 'application/json' // Define o cabeÃ§alho para indicar que o corpo da requisiÃ§Ã£o Ã© JSON.
        },
        // Converte o objeto com o texto, idioma de origem e idioma de destino em uma string JSON e define como o corpo da requisiÃ§Ã£o.
        body: JSON.stringify({
            text: inputText,
            source_lang: sourceLang, // Corrigido para enviar 'source_lang' em vez de 'sourceLang'.
            target_lang: targetLang  // Corrigido para enviar 'target_lang' em vez de 'targetLang'.
        })
    })
    .then(response => response.json()) // Converte a resposta do servidor para JSON.
    .then(data => {
        // Se a resposta contÃ©m um erro, exibe um alerta com a mensagem de erro.
        if (data.error) {
            alert(data.error);
        } else {
            // Caso contrÃ¡rio, define o texto traduzido no elemento com id 'outputText'.
            document.getElementById('outputText').value = data.translated_text;
        }
    })
    .catch(error => {
        // Se ocorrer um erro durante a requisiÃ§Ã£o, exibe o erro no console do navegador.
        console.error('Error:', error);
        document.getElementById('outputText').value = "Erro na traduÃ§Ã£o. Tente novamente.";
    });
}
